import React, { useState } from 'react';
import './App.css';
import ProductCard from './components/ProductCard';
import { IoCartOutline } from 'react-icons/io5';

//  src/assets  import  
import shoe1 from './assets/shoe1.jpg';
import shoe2 from './assets/shoe2.jpg';
import shoe3 from './assets/shoe3.jpg';
import shoe4 from './assets/shoe4.jpg';
import shoe5 from './assets/shoe5.jpg';
import shoe6 from './assets/shoe6.jpg';

function App() {
  const [cartCount, setCartCount] = useState(0);

  const products = [
    { id: 1, brand: ' A', description: '   ', price: '35,000', image: shoe1 },
    { id: 2, brand: ' B', description: '   ', price: '42,000', image: shoe2 },
    { id: 3, brand: ' C', description: '   ', price: '29,000', image: shoe3 },
    { id: 4, brand: ' D', description: '  ', price: '58,000', image: shoe4 },
    { id: 5, brand: ' E', description: '  ', price: '47,000', image: shoe5 },
    { id: 6, brand: ' F', description: '   ', price: '53,000', image: shoe6 },
  ];

  //   :   
  const handleAddToCart = () => {
    setCartCount(prev => prev + 1);
    //     (  )
    // console.log('add clicked, current will increment next render');
  };

  return (
    <div className="app">
      <header className="header">
        <div className="cart-wrap" role="region" aria-label="cart">
          <IoCartOutline size={24} color="#fff" />
          {/*    : {cartCount > 0 && ...} */}
          <span data-testid="cart-count" className="cart-count">{cartCount}</span>
        </div>
      </header>

      <div className="title-section">
        <h2>  </h2>
        <p> {products.length}  .</p>
      </div>

      <div className="product-list">
        {products.map(item => (
          <ProductCard
            key={item.id}
            image={item.image}
            title={item.brand}
            description={item.description}
            price={item.price}
            onAdd={handleAddToCart}   // <--  
          />
        ))}
      </div>
    </div>
  );
}

export default App;
